import React, { lazy, Suspense, useState, useEffect, useContext } from "react";
import { Cascader, ConfigProvider } from "antd";
const { SHOW_CHILD } = Cascader;
const SleepingTimeline = lazy(() => import("./Components/SleepingTimeline"));
const SleepEvents = lazy(() => import("./Components/SleepEvents"));
const DurationStatistic = lazy(() => import("./Components/DurationStatistic"));
const DurationDistribution = lazy(() =>
  import("./Components/DurationDistribution")
);
const EfficiencyStatistic = lazy(() =>
  import("./Components/EfficiencyStatistic")
);
const DeepSleepPercentageDistribution = lazy(() =>
  import("./Components/DeepSleepPercentageDistribution")
);
const DeepSleepPercentageStatistic = lazy(() =>
  import("./Components/DeepSleepPercentageStatistic")
);
const EfficiencyDistribution = lazy(() =>
  import("./Components/EfficiencyDistribution")
);
const ApneaIndexStatistic = lazy(() =>
  import("./Components/ApneaIndexStatistic")
);
const ApneaIndexDistribution = lazy(() =>
  import("./Components/ApneaIndexDistribution")
);
const BedExitDurationStatistic = lazy(() =>
  import("./Components/BedExitDurationStatistic")
);
const NumberOfBedExitStatistic = lazy(() =>
  import("./Components/NumberOfBedExitStatistic")
);
const DistributionOfBedExitTimes = lazy(() =>
  import("./Components/DistributionOfBedExitTimes")
);
const TimeToFallAsleepStatistic = lazy(() =>
  import("./Components/TimeToFallAsleepStatistic")
);
const DistributionOfTimeToFallAsleep = lazy(() =>
  import("./Components/DistributionOfTimeToFallAsleep")
);
const DistributionOfDailyRoutine = lazy(() =>
  import("./Components/DistributionOfDailyRoutine")
);
const DataAnalysisSlider = lazy(() =>
  import("./Components/DataAnalysisSlider")
);
const SleepScoreTails = lazy(() => import("./Components/SleepScoreTails"));
const PieChart = lazy(() => import("@/Components/GraphAndChart/PieChart"));
import { CustomContext } from "@/Context/UseCustomContext";

function ChartWrapper({ height = 480, children, className }) {
  return (
    <div className={`h-[${height}px] ${className}`}>
      <Suspense fallback={<div>Loading...</div>}>{children}</Suspense>
    </div>
  );
}
export default function SleepTab() {
  const options = [
    {
      label: "Light and Deep Sleep",
      value: "a",
      children: [
        {
          label: "Sleep Duration Statistic",
          value: "duration",
        },
        {
          label: "Sleep Duration Distribution",
          value: "durationDistribution",
        },
        {
          label: "Sleep Efficiency Statistic",
          value: "efficiencyStatistic",
        },
        {
          label: "Sleep Efficiency Distribution",
          value: "efficiencyDistribution",
        },
        {
          label: "Deep Sleep Percentage Statistic",
          value: "deepSleepPercentageStatistic",
        },
        {
          label: "Deep Sleep Percentage Distribution",
          value: "deepSleepPercentageDistribution",
        },
      ],
    },
    {
      label: "Sleep Apnea",
      value: "b",
      children: [
        {
          label: "Apnea Index Statistic",
          value: "apneaIndexStatistic",
        },
        {
          label: "Apnea Index Distribution",
          value: "apneaIndexDistribution",
        },
      ],
    },
    {
      label: "Bed Exit",
      value: "c",
      children: [
        {
          label: "Number of Bed Exit Statistic",
          value: "numberOfBedExitStatistic",
        },
        {
          label: "Bed Exit Duration Statistic",
          value: "bedExitDurationStatistic",
        },
        {
          label: "Distribution of Bed Exit Times",
          value: "distributionOfBedExitTimes",
        },
      ],
    },
    {
      label: "Daily Routine",
      value: "d",
      children: [
        {
          label: "Distribution of Daily Routine",
          value: "distributionOfDailyRoutine",
        },
      ],
    },
    {
      label: "Fall Asleep",
      value: "e",
      children: [
        {
          label: "Distribution of Time to Fall Asleep",
          value: "distributionOfTimeToFallAsleep",
        },
        {
          label: "Time to Fall Asleep Statistic",
          value: "timeToFallAsleepStatistic",
        },
      ],
    },
  ];
  const [selectedValues, setSelectedValues] = useState([]);
  const handleCascaderChange = (value) => {
    const flattenedValues = value.map((path) => path[path.length - 1]);
    console.log("Flattened selected values:", flattenedValues);
    setSelectedValues(flattenedValues);
  };
  const { sleepData } = useContext(CustomContext);

  const isSelected = (value) => {
    // Show all charts if no specific selections are made
    if (selectedValues.length === 0) return true;
    const result = selectedValues.includes(value);
    return result;
    console.log(`Checking if ${value} is selected:`, result);
  };

  return (
    <div className="flex flex-col gap-6 mt-6 h-full">
      <div
        id="sleepOverview"
        className="flex gap-6 flex-col md:flex-row lg:flex-row h-full"
      >
        <div
          id="dataanalysisSleepTails"
          className="w-full md:w-[67%] flex flex-col gap-6 !h-full justify-between"
        >
          <SleepScoreTails />
          <div id="dataAnalysis" className="h-full">
            <DataAnalysisSlider />
          </div>
        </div>
        <div id="sleepscoreGraph" className="w-full md:w-[33%]">
          <Suspense fallback={<div>Loading...</div>}>
            <div className="w-full h-full bg-white rounded-lg p-5 flex flex-col gap-4">
              <h1 className="text-primary text-2xl font-bold leading-none">
                Sleep Score
              </h1>
              <PieChart
                sleepData={sleepData?.sleep_index_common_list}
                sleepScore={sleepData?.score}
              />
            </div>
          </Suspense>
        </div>
      </div>

      <ChartWrapper height={"auto"}>
        <SleepingTimeline />
      </ChartWrapper>
      <ChartWrapper height={"auto"}>
        <SleepEvents />
      </ChartWrapper>
      <div className="flex justify-between items-center gap-6 mt-10 bg-[#7F87FC] rounded-lg p-5 ">
        <h1 className="text-white text-2xl font-bold leading-none">
          Reports Analysis
        </h1>
        <ConfigProvider
          theme={{
            token: {
              fontFamily: "Baloo2",
              colorPrimary: "#8086AC",
              colorLinkActive: "#8086AC",
              colorLinkHover: "#8086AC",
              colorLink: "#8086AC",
            },
          }}
        >
          <Cascader
            style={{
              width: "300px",
            }}
            options={options}
            placeholder="Select Specific Analysis"
            multiple
            maxTagCount="responsive"
            size="large"
            showCheckedStrategy={SHOW_CHILD}
            onChange={handleCascaderChange}
            defaultValue={selectedValues}
          />
        </ConfigProvider>
      </div>
      <div className="grid grid-cols-1 gap-6 md:grid-cols-2 lg:grid-cols-2">
        {isSelected("duration") && (
          <ChartWrapper>
            <DurationStatistic />
          </ChartWrapper>
        )}
        {isSelected("durationDistribution") && (
          <ChartWrapper>
            <DurationDistribution />
          </ChartWrapper>
        )}
        {isSelected("efficiencyStatistic") && (
          <ChartWrapper>
            <EfficiencyStatistic />
          </ChartWrapper>
        )}
        {isSelected("efficiencyDistribution") && (
          <ChartWrapper>
            <EfficiencyDistribution />
          </ChartWrapper>
        )}
        {isSelected("deepSleepPercentageStatistic") && (
          <ChartWrapper>
            <DeepSleepPercentageStatistic />
          </ChartWrapper>
        )}
        {isSelected("deepSleepPercentageDistribution") && (
          <ChartWrapper>
            <DeepSleepPercentageDistribution />
          </ChartWrapper>
        )}
        {isSelected("apneaIndexStatistic") && (
          <ChartWrapper>
            <ApneaIndexStatistic />
          </ChartWrapper>
        )}
        {isSelected("apneaIndexDistribution") && (
          <ChartWrapper>
            <ApneaIndexDistribution />
          </ChartWrapper>
        )}
        {isSelected("bedExitDurationStatistic") && (
          <ChartWrapper>
            <BedExitDurationStatistic />
          </ChartWrapper>
        )}
        {isSelected("numberOfBedExitStatistic") && (
          <ChartWrapper>
            <NumberOfBedExitStatistic />
          </ChartWrapper>
        )}
        {isSelected("distributionOfBedExitTimes") && (
          <ChartWrapper className="col-span-2">
            <DistributionOfBedExitTimes />
          </ChartWrapper>
        )}
        {isSelected("distributionOfDailyRoutine") && (
          <ChartWrapper className="col-span-2">
            <DistributionOfDailyRoutine />
          </ChartWrapper>
        )}
        {isSelected("timeToFallAsleepStatistic") && (
          <ChartWrapper>
            <TimeToFallAsleepStatistic />
          </ChartWrapper>
        )}
        {isSelected("distributionOfTimeToFallAsleep") && (
          <ChartWrapper>
            <DistributionOfTimeToFallAsleep />
          </ChartWrapper>
        )}
      </div>
    </div>
  );
}

export const SleepScoreTailsData = [
  {
    title: "Avg. Deep Sleep",
    value: 67,
    dataType: "percentage",
  },
  {
    title: "Avg. Sleep Efficiency",
    value: 60,
    dataType: "percentage",
  },
  {
    title: "Avg. Apnea Index ",
    value: 6,
    dataType: "index",
  },
  {
    title: "Avg. Bed Exit Frequency",
    value: 5,
    dataType: "index",
  },
];
